<template>
  <div name="cooperation">
    <div class="contact_container4">
      <div class="tab4_box">
        <div
          class="tab4_title"
          style="display: flex;justify-content: space-between;"
        >
          <span style="flex:1">客户主负责人</span>

          <span
            v-if="PermissionObj['cooperationinfo_update'] != undefined&&collaboratorData.principal.id==$store.getters.userInfo.id"             
            style="line-height:30px;padding:0;flex:1;color:#03c061;text-align: right;"            
            @click="routerTo(2)"
          >
            变更主要负责人
          </span>
        </div>
        <div class="tab4-list">
          <div class="tab4-left-box">
            <div class="img-box">
              <img src>
            </div>
          </div>
          <div class="tab4-right-box">
            <p class="title-p">
              {{ collaboratorData.principal.name }}
            </p>
            <p class="value-p">
              {{ collaboratorData.principal.roleVOS[0].roleName }}
            </p>
          </div>
        </div>

        <div
          v-if="collaboratorData.leaderUserVOS.length!=0"
          class="tab4_title"
        >
          <span>上级领导</span>
        </div>
        <div
          v-for="(item) in collaboratorData.leaderUserVOS"
          :key="item.uid"
          class="tab4-list"
        >
          <div class="tab4-left-box">
            <div class="img-box">
              <img src>
            </div>
          </div>
          <div class="tab4-right-box">
            <p class="title-p">
              {{ item.name }}
            </p>
            <p class="value-p">
              {{ item.roleVOS[0].roleName }}
            </p>
          </div>
        </div>

        <div
          v-if="collaboratorData.collaboratorUserVOS.length!=0"
          class="tab4_title"
        >
          <span>协作人</span>
        </div>
        <div
          v-for="(item) in collaboratorData.collaboratorUserVOS"
          :key="item.uid"
          class="tab4-list"
        >
          <div class="tab4-left-box">
            <div class="img-box">
              <img src>
            </div>
          </div>
          <div class="tab4-right-box">
            <p class="title-p">
              {{ item.name }}
            </p>
            <p class="value-p">
              {{ item.roleVOS[0].roleName }}
            </p>
          </div>
          <!-- <span class="iconfont iconshanchu theme-colors delete-icon" /> -->
        </div>
      </div>

      <div
        v-if="PermissionObj['cooperationinfo_update'] != undefined"
        class="tab-footer"
      >
        <van-button
          v-if="!isCollaboraType"
          to="/addCooperation"
          class="gradual-btn"
          style="width:100%;"
          type="primary"
        >
          管理协作人
        </van-button>
      </div>
    </div>
  </div>
</template>
<script>
// import { api, get } from "@api";
// import { mapState, mapMutations } from 'vuex'
// import { formatDate, isNull } from '@/utils'
import { collaboratorList } from '@/service/customer'
export default {
  name: 'Cooperation',
  data() {
    return {
      PermissionObj:this.$store.state.powerList.PermissionObj,///权限
      collaboratorData:{
        principal:{
          name:''
        },
        leaderUserVOS:[],
        collaboratorUserVOS:{}
      },
      isCollaboraType:false,//标识当前用户是否是协作人身份
    }
  },
  computed: {},
  created() { 
    this.getCollaborator()
  },
  methods: {
    routerTo(type){
      if(type==2){
        this.$router.push({
          path:'/addCooperation',
          query:{
            type:2
          }
        })
      }else{
        this.$router.push({
          path:'/addCooperation',
          query:{
            id:this.$route.query.id
          }
        })
      }
      
    },
    //协作人list
    getCollaborator(){
      console.log(this.$store.getters.userData)
      collaboratorList({
        id:this.$route.query.id
      }).then(res=>{
        this.collaboratorData = res.data
        console.log('协作人list',res)
        if(res.data.collaboratorUserVOS){
          res.data.collaboratorUserVOS.map(d=>{
            if(d.id == this.$store.getters.userInfo.id){
              this.isCollaboraType = true
            }
          })
        }
        
        
      })
    }
  },
}
</script>
