<script>
// import { api, get } from "@api";
// import { company } from '@/service/customer'
// import { mapState, mapMutations } from 'vuex'
// import { formatDate, isNull } from '@/utils'
import { firmInfo } from '@/service/customer'
export default {
  name: "Info",
  data() {
    return {
      tabData: {
        fuLiContactsInfoVO:[{contactsPost:''},{}]
      },
      PermissionObj: this.$store.state.powerList.PermissionObj ///权限
    }
  },
  computed: {},
  created() {
    this.getData()
  },
  methods: {    
    getData() {
      const params = {
        id: this.$route.query.id
      }
      firmInfo(params).then((res) => {
        let DArr = res.data.fuLiContactsInfoVO
        DArr = DArr || []
        let one = null
        let two = null
        let UserArr = []
        DArr.map((d, idx) => {          
          if (d.contactsIdentification === '第一联络人') {
            one=d
          }
          if (d.contactsIdentification === '第二联络人') {
            two=d
          }
          if (d.contactsIdentification != '第二联络人'&&d.contactsIdentification != '第一联络人') {
            UserArr.push(d)
          }
          return d
        })   
        if(two){
          UserArr.unshift(two)
        } 
        if(one){
          UserArr.unshift(one)
        }        
        res.data.fuLiContactsInfoVO = UserArr
        this.tabData = res.data
        console.log('企业信息firmInfo',res)
        
        this.$store.commit('SET_USER_DATA', res.data)
      })
    },
    changeUser() {
      // to="/addCustomer"
      console.log('to="/addCustomer"')
      this.$router.push({
        path:"/addCustomer",
        query:{
          id:this.$route.query.id,
          OTP:1
        }
      })
      this.$store.commit("SET_UserType", true)
    }
  }
}
</script>
