<template>
  <div name="commerce">
    <div class="contact_container7">
      <div class="contact_container_er">
        <van-list
          v-model="loadingLog"
          :finished="finishedLog"
          :finished-text="finished_text"
          :error.sync="error"
          error-text="请求失败，点击重新加载"
          @load="onLoad()"
        >
          <div
            v-for="(item,idx) in meetList"
            :key="idx"
            class="item-list"
            @click="itemDetails(item.id)"
          >
            <div class="item-left">
              <div
                :class="{'xian':true,'xianTop':item.top,'xianBottom':item.bottom}"
              />
              <div :class="{'circular':true,'theme-background-colors':item.top||item.bottom}" />
            </div>
            <div class="item-right">
              <div class="right">
                <div class="top">
                  <h4 class="text_h4">
                    {{ item.companyName }}
                  </h4>
                  <p class="text_p">
                    动作:上会
                  </p>
                  <p class="text_p">
                    最后跟进时间:{{ item.createTime }}
                  </p>
                  <p class="text_p">
                    所属项目:{{ item.information.projectName }}
                  </p>
                  <span
                    v-if="!item.operationInfo && PermissionObj['messageboard_insert'] != undefined"
                    style="position:absolute;right:10px;bottom:65px;"
                    class="iconfont iconliuyan theme-colors"
                    @click.stop="addMessage(item)"
                  />
                  <div
                    v-if="item.content !== null"
                    class="message"
                    @click.stop="showMessage(item)"
                  >
                    <div class="message-left">
                      {{ item.content }}
                    </div>
                    <div
                      class="message-right"                      
                    >
                      <span>{{ item.boaCreateTime }}</span>
                      <van-icon
                        style="position:relative;top:2px;"
                        name="arrow"
                      />
                    </div>

                    <span
                      v-if="!item.isRead"
                      style="position:absolute;right:0;top:1px;color:#ff0021;"
                      class="iconfont iconnew"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-list>
      </div>
      
      <div
        v-if="PermissionObj['metting_insert'] != undefined"
        class="tab-footer"
      >
        <van-button
          class="gradual-btn"
          style="width:100%;"
          type="primary"
          to="/addCommerce"
        >
          新建上会事件
        </van-button>
      </div>
    </div>

    <!-- 留言板弹层 -->
    <van-popup v-model="messageBoard">
      <div class="messageBoard">
        <div class="header">
          留言板
        </div>
        <div
          v-for="(item,idx) in messageLists"
          :key="idx"
          class="container"
        >
          <div class="container_header">
            <span style="float:left;">{{ item.author }}</span>
            <span style="float:right;">{{ item.createTime }}</span>
          </div>
          <div class="container_p">
            {{ item.content }}
          </div>
        </div>
      </div>
    </van-popup>

    <!--新增留言弹层 -->
    <van-popup v-model="AddmessageBoard">
      <div class="messageBoard">
        <div class="header">
          留言板
        </div>
        <div class="container">
          <textarea
            v-model="messageStr"
            :class="messageError ? 'textarea_no' :'textarea_yes'"
            placeholder="请输入留言"
          />
        </div>
        <van-row>
          <van-col span="12">
            <van-button
              class="clear-btn"
              style="width:100%;"
              square
              type="primary"
              @click="messageClear()"
            >
              取消
            </van-button>
          </van-col>
          <van-col span="12">
            <van-button
              class="confirm-btn"
              style="width:100%;"
              square
              type="primary"
              @click="messageSave()"
            >
              确定
            </van-button>
          </van-col>
        </van-row>
      </div>
    </van-popup>
  </div>
</template>
<script>
// import { api, get } from "@api";
// import { mapState, mapMutations } from 'vuex'
import { isNull,addMark } from '@/utils'
import { projectList,messageList,addMessages } from '@/service/customer'

export default {
  name: 'Commerce',
  data() {
    return {
       PermissionObj:this.$store.state.powerList.PermissionObj,///权限
      messageBoard: false,
      AddmessageBoard: false,
      messageStr: '',
      messageError: false,
      meetList:[],
      totalLog: null,
      loadingLog: false,
      finishedLog: false,
      finished_text: '没有更多数据啦',
      error: false,
      pageLog: 1,
      size: 5,
      messageLists:[]
    }
  },
  computed: {},
  created() {
  },
  methods: {
    // 加载数据
    onLoad() {
      const that = this
      if (this.meetList.length === this.totalLog) {
        this.finishedLog = true // 数据全部加载完成
        this.loadingLog = false// 加载状态结束
        return
      }
      // 下拉加载事件
      setTimeout(() => {
        that.getProjectList()
      }, 1000)      
    },
    getProjectList() {
      const params = {
        menuType: 4,
        page: this.pageLog,
        size: this.size,
      }
      projectList(params).then((res) => {
        // console.log('projectList', res)
        this.meetList = this.meetList.concat(res.data)
        this.meetList = addMark(this.meetList,3)    
        this.totalLog = res.total
        this.pageLog += 1
        this.loadingLog = false// 加载状态结束
        // this.quoteList = addMark(this.quoteList,3)  
      })
    },    
    
    //唤醒留言表单
    addMessage(item) {
      // console.log('item',item)
      this.setingItem = item
      this.messageBoard = false
      setTimeout(() => {
        // 加载状态结束
        this.AddmessageBoard = true
      }, 300)
    },
    //展示留言
    showMessage(item){
      this.messageBoard = !this.messageBoard
      this.setingMessage = item
      item.isRead = true
      this.getMessageList(item)
    },
    //获取留言列表
    getMessageList(item){
      // console.log('获取留言列表',item)
      let params = {
        eventType:8,
        eventId:item.id,
        messageId:item.messageId,
      }
      messageList(params).then(res=>{
        this.messageLists = res.data || []
        // console.log('获取留言',res)
      })
    },
    //新增留言
    setMessage(){      
      let params = {
        author:this.$store.getters.userInfo.name,
        authorUserId:this.$store.getters.userInfo.id,
        eventId:this.setingItem.eventId,
        content:this.messageStr,
        eventType:8
      }
      // console.log('新增留言',params)
      addMessages(params).then(res=>{
        // console.log('获取留言',res)
        this.messageStr = ''
        this.messageError = false     
        this.AddmessageBoard = false
        this.getInit()
        this.onLoad()
      })
    },
    getInit(){
      this.page=1
      this.pageLog = 1
      this.meetList = []
      this.totalLog = null
      this.total = null
    },
    // 留言板取消事件
    messageClear() {      
      this.AddmessageBoard = false
      this.messageStr = ''
    },
    // 留言保存事件
    messageSave() {
      // console.log('留言保存事件',this.messageStr)
      this.setMessage()
      if (isNull(this.messageStr)) {
        this.messageError = true
        return
      }      
    },
    //事件详情
    itemDetails(id){
      this.$router.push({
        path:'/addCommerce',
        query:{
          id,
        }
      })
    }
  },
}
</script>
