<script>
// import { api, get } from "@api"
import { firmInfo } from '@/service/customer'
// import { mapState, mapMutations } from 'vuex'
// import { formatDate, isNull } from '@/utils'
import customerDynamic from './tab-dynamic.vue'
import customerInfo from './tab-info.vue'
import customerContacts from './tab-contacts.vue'
import customerCooperation from './tab-cooperation.vue'
import customerVisit from './tab-visit.vue'
import customerCreation from './tab-creation.vue'
import customerQuotation from './tab-quotation.vue'
import customerCommerce from './tab-commerce.vue'
import customerInvestigation from './tab-investigation.vue'
import customerSigning from './tab-signing.vue'
import customerLoan from './tab-loan.vue'
import customerAfterloan from './tab-afterloan.vue'

export default {
  name: 'Customer',
  components: {
    'customer-dynamic': customerDynamic,
    'customer-info': customerInfo,
    'customer-contacts': customerContacts,
    'customer-cooperation': customerCooperation,
    'customer-visit': customerVisit,
    'customer-creation': customerCreation,
    'customer-quotation': customerQuotation,
    'customer-commerce': customerCommerce,
    'customer-investigation': customerInvestigation,
    'customer-signing': customerSigning,
    'customer-loan': customerLoan,
    'customer-afterloan': customerAfterloan,
  },
  data() {
    return {
      active:0,
      id: '',
      tabData: {},
      showtype: [],
      TTarrName: ['动态', '信息', '联系人', '协作人', '拜访', '立项', '报价', '尽调', '上会', '签约', '投放', '贷后'],
      TTarrType: [true, true, true, true, true, true, true, true, true, true, true, true],
    }
  },
  watch:{
    active(newV,oldV){
      console.log('active','n=',newV,'o=',oldV)
      this.$store.commit('SET_ACTIVE',newV)
      return 
    }
  },
  destroyed() {
    // this.$store.commit('SET_ACTIVE', 0)
  },
  created() {
    // this.$store.commit('SET_USER_DATA', {})
    this.active = this.$store.getters.active
    this.getRouterData()
    console.log('this.showtype', this.showtype)
    this.getData()
    this.getPowerList()
  },
  methods: {
    getRouterData() {
      this.id = this.$route.query.id
    },
    getData() {
      const params = {
        id: this.id,
      }
      firmInfo(params).then((res) => {
        this.tabData = res.data
        console.log('企业信息firmInfo',res)
        this.$store.commit('SET_USER_DATA', res.data)
      })
    },
    getPowerList() {
      const { powerList } = this.$store.getters
      // eslint-disable-next-line array-callback-return
      this.TTarrName.map((d, idx) => {
        this.TTarrType[idx] = powerList.TTarr.indexOf(d) > -1
      })
    },

  },
}
</script>
