  methods: {
    changSearch() { },
    search(){
      this.getLendPage(this.searchObj)      
    },
    //获取当前页面数据
    getLendPage(searchObj) {
      let params = searchObj || {}
      params.size = 1000
      lendPage(searchObj).then((res) => {
        res.data.data = res.data.data || []
        console.log('lendPage', res)
        this.lendList = res.data.data
      })
    },
    routerTo(id,type) {
      this.$router.push({
        path: '/addLoan',
        query: {
          id,
          OTP:type
        },
      })
    },
  },
}
