<template>
  <div name="visit">
    <div class="contact_container5">
      <div class="tab5_box">
        <div class="tab5-search">
          <form
            action=""
            @submit.prevent="searchChange"
          >
            <input
              v-model="visitName"
              type="text"
              placeholder="请输入拜访人姓名"
            >
          </form>
          

          <div
            class="selectTime"
            @click="showSelectTime()"
          >
            {{ selectTimeStr== '' ? '请选择时间' : selectTimeStr }}
            <van-icon
              v-if="selectTimeStr"
              name="cross"
              class="clearTime"
              @click.stop="clearTime"
            />
            <!-- <span >清除</span> -->
          </div>
        </div>
        <van-list
          v-model="loading"
          :finished="finished"
          :finished-text="finished_text"
          :error.sync="error"
          error-text="请求失败，点击重新加载"
          @load="onLoad()"
        >
          <div
            v-for="(itm,idx) in VTList"
            :key="idx"
            class="tab5-list"
            @click.stop="routerTo(itm,2)"
          >
            <div class="tab5-list-tilte">
              <span class="span1">{{ itm.companyName }}</span>
              <span
                v-if="PermissionObj['callon_update'] != undefined"
                class="span2 theme-colors"
                @click.stop="routerTo(itm,1)"
              >编辑</span>
            </div>
            <div class="tab5-list-p">
              <span class="tab5-list-p-label">
                拜访内容:
              </span>
              <span
                class="tab5-list-p-value"
                style="line-height: 19px;"
              >
                {{ itm.visitContent }}
              </span>
            </div>
            <div class="tab5-list-p">
              <span class="tab5-list-p-label">
                签到人:
              </span>
              <span class="tab5-list-p-value">
                <span>{{ itm.visitName }}</span>
                <span>{{ itm.updateTime }}</span>
              </span>
            </div>
          </div>
        </van-list>
      </div>

      <div
        v-if="PermissionObj['callon_insert'] != undefined"
        class="tab-footer"
      >
        <van-button
          class="gradual-btn"
          style="width:100%;"
          type="primary"
          to="/addVisit"
        >
          新建拜访
        </van-button>
      </div>
    </div>

    <!-- tab5时间选择 -->
    <van-popup
      v-model="selectTime"
      position="bottom"
      :overlay="true"
    >
      <van-datetime-picker
        v-model="searchDate"
        type="date"
        :max-date="maxDate"
        :formatter="formatter"
        @confirm="startconfirm($event)"
        @cancel="startconfirm()"
      />
    </van-popup>
  </div>
</template>
<script>
// import { api, get } from "@api";
// import { mapState, mapMutations } from 'vuex'
import { VisitList } from '@/service/customer'
import { formatDate, isNull } from '@/utils'

export default {
  name: 'Visit',
  data() {
    return {
      PermissionObj:this.$store.state.powerList.PermissionObj,///权限
      selectTime: false,
      searchDate:new Date(),
      maxDate: new Date(),
      selectTimeStr: '',
      VTList: [],
      total: null,
      page: 1,
      size: 5,
      tabIndex: 1,
      loading: false,
      finished: false,
      finished_text: '没有更多数据啦',
      error: false,
      searchObj: null,
      visitName:''
    }
  },
  computed: {},
  created() {
    // this.getVisitList()
  },
  methods: {
    clearTime(){
      this.selectTimeStr = null
      this.initList()
    },
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } if (type === 'month') {
        return `${value}月`
      }
      return `${value}日`
    },
    showSelectTime() {
      this.selectTime = !this.selectTime
    },
    startconfirm(e) {
      if (isNull(e)) {
        this.selectTime = false
      } else {
        const time = formatDate(new Date(e), 'yyyy-MM-dd')
        console.log('time',time)
        this.selectTimeStr = time
        this.selectTime = false
        this.searchChange()
      }
    },
    // 获取拜访列表
    getVisitList() {
      const params = {
        userId: this.$store.getters.userInfo.id,
        visitName:this.visitName,
        visitTime:this.selectTimeStr,
        page:this.page,
        size:this.size
      }
      VisitList(params).then((res) => {
        console.log('getVisitList', res)
        this.VTList = this.VTList.concat(res.data.data)
        this.total = res.data.total
        this.page += 1
        this.loading = false// 加载状态结束
      })
    },
    // 加载数据
    onLoad(searchObj) {
      const that = this
      if (this.VTList.length === this.total) {
        this.finished = true // 数据全部加载完成
        this.loading = false// 加载状态结束
        return
      }
      searchObj = searchObj || this.searchObj
      // 下拉加载事件
      if (that.tabIndex === 1) {
        setTimeout(() => {
          that.getVisitList(searchObj)
        }, 1000)
      }
    },
    //初始化列表配置
    initList(){
      this.total = null
      this.page = 1
      this.loading = true// 加载状态结束
      this.VTList = []
      this.onLoad()
    },
    //formChange
    searchChange(){
      this.initList()
    },
    //
    routerTo(itm,OTP) {
      if(itm.createUserId!=this.$store.getters.userInfo.id&&OTP==1){
        this.$toast('非拜访创建人无权编辑该拜访！')
        return
      }
      this.$router.push({
        path: '/addVisit',
        query: {
          id:itm.visitId,
          OTP
        },
      })
    },
  },
}
</script>
