<template>
  <div name="contacts">
    <div class="contact_container3">
      <div class="tab3_box">
        <van-list
          v-model="loading"
          :finished="finished"
          :finished-text="finished_text"
          :error.sync="error"
          error-text="请求失败，点击重新加载"
          @load="onLoad()"
        >
          <div
            v-for="(itm,idx) in userList"
            :key="idx"
            class="tab3-list"
          >
            <div class="tab3-list-tilte">
              <span class="span1">{{ itm.contactsName }}</span>
              <span
                class="span2"
              >{{ itm.contactsIdentification }}</span>
              <span
                v-if="!itm.contactsIdentification"
                class="span2 theme-colors"
                @click="setTopUser(itm.contactsId)"
              >置顶</span>
            </div>
            <div class="tab3-list-p">
              <div class="text-label">
                对应客户
              </div>
              <div class="text-info">
                {{ itm.companyName }}
              </div>
            </div>
            <div class="tab3-list-p">
              <div class="text-label">
                职务
              </div>
              <div class="text-info">
                {{ itm.contactsPost }}
              </div>
            </div>
            <div class="tab3-list-p">
              <div class="text-label">
                手机
              </div>
              <div class="text-info">
                {{ itm.contactsMoblie }}
              </div>
            </div>
            <div class="tab3-list-p">
              <div class="text-label">
                性别
              </div>
              <div class="text-info">
                {{ itm.contactsSex }}
              </div>
            </div>
            <div class="tab3-list-p">              
              <div class="text-info">
                <span style="float:right;">
                  <span
                    v-if="PermissionObj['concatinfo_list'] != undefined"
                    class="theme-colors"
                    @click="openUser(itm.contactsId,1)"
                  >查看</span>
                  <span
                    class="theme-colors"
                    style="position:relative;top:-1px;margin:0 5px;"
                  >|</span>
                  <span
                    v-if="PermissionObj['concatinfo_update'] != undefined"
                    class="theme-colors"
                    @click.stop="openUser(itm.contactsId,2)"
                  >编辑</span>
                  <span
                    v-if="!itm.contactsIdentification"
                    class="theme-colors"
                    style="position:relative;top:-1px;margin:0 5px;"
                  >|</span>
                  <span                  
                    v-if="PermissionObj['concatinfo_update'] != undefined&&!itm.contactsIdentification"
                    class="theme-colors"                    
                    @click="boxShow(itm)"                    
                  >删除</span>
                </span>
              </div>
            </div>
            <a :href="'tel:'+itm.contactsMoblie">
              <span class="iconfont iconicon_tel theme-colors list-tel" />
            </a>
            <span
              v-if="itm.externalContactIds"
              class="theme-colors list-wx"
              @click="createdMeet(itm.externalContactIds)"
            > <img
              style="width:20px;height:20px;"
              src="../../assets/img/wxLOGO.jpg"
              alt=""
            > </span>
          </div>
        </van-list>
      </div>

      <div
        v-if="PermissionObj['concatinfo_insert'] != undefined"
        class="tab-footer"
      >
        <van-button
          class="gradual-btn"
          style="width:100%;"
          type="primary"
          to="/addContact"
        >
          新建联络人
        </van-button>
      </div>
    </div>
    <van-popup
      v-model="show"
      class="Tit-box"
    >
      <div class="Tit-box-title">
        提示
      </div>
      <div class="Tit-box-cont">
        确认删除？
      </div>
      <div class="Tit-box-buts">
        <van-button
          class="Tit-box-but"
          type="primary"
          square
          @click="deleteThis"
        >
          确认
        </van-button>
        <van-button
          class="Tit-box-but"
          square
          @click="boxShow"
        >
          取消
        </van-button>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { deleteCallUser, callUserList, setTopCallUser,topUser } from '@/service/customer'
export default {
  name: 'Contacts',
  data() {
    return {
      PermissionObj:this.$store.state.powerList.PermissionObj,///权限
      userList: [],
      loading: false,
      finished: false,
      finished_text: '没有数据了哦',
      error: false,
      total: null,
      page: 1,
      size: 10,
      show:false
    }
  },
  computed: {},
  created() {
    // this.getData();
  },
  methods: {   
    createdMeet(UID){
      wx.openEnterpriseChat({
					// 注意：userIds和externalUserIds至少选填一个，且userIds+openIds总数不能超过2000。
					externalUserIds : UID, //参与会话的企业成员列表，格式为userid1;userid2;...，用分号隔开。
					groupName : '', // 必填，会话名称。单聊时该参数传入空字符串""即可。
					success : function(res) {
            console.log('创建会话',res)
						// 回调
					},
					fail : function(res) {
						if (res.errMsg.indexOf('function not exist') > -1) {
							alert('版本过低请升级')
						}
					}
				})
      console.log('openEnterpriseChat回话接口',wx.openEnterpriseChat) 
    }, 
    boxShow(itm){
      this.show = !this.show
      if(!this.show)this.delItm = null
      else this.delItm = itm
      console.log('this.delItm',this.delItm)
    },
    onLoad() {
      if (this.userList.length === this.total) {
        this.finished_text = '没有更多数据啦'
        this.finished = true // 数据全部加载完成
        this.loading = false// 加载状态结束
        return
      }
      // 下拉加载事件
      setTimeout((res) => {
        // console.log(res)
        this.getData()
      }, 1000)
    },
    // 删除联络人
    deleteThis() {
      // console.log('Uid', Uid)
      deleteCallUser({ contactsId: this.delItm.contactsId }).then((res) => {
        // console.log('deleteCallUser', res)
        if (res.data) {
          // eslint-disable-next-line array-callback-return
          this.userList.map((d, idx) => {
            if (d.contactsId === this.delItm.contactsId) {
              this.userList.splice(idx, 1)
            }
          })
          this.$notify({
            message: '删除成功',
            duration: 1000,
            background: 'green',
          })
          this.boxShow()
        } else {
          this.$notify('删除失败')
        }
      })
    },
    update() { },
    getData() {
      callUserList(
        { companyId: this.$store.getters.userData.companyId, page: this.page, size: this.size },
      ).then((res) => {
        let DArr = res.data.data
        DArr = DArr || []
        let one = {}
        let two = {}
        let UserArr = []
        DArr.map((d, idx) => {          
          if (d.contactsIdentification === '第一联络人') {
            one=d
          }
          if (d.contactsIdentification === '第二联络人') {
            two=d
          }
          if (d.contactsIdentification != '第二联络人'&&d.contactsIdentification != '第一联络人') {
            UserArr.push(d)
          }
          return d
        })   
        if(two.companyName){
          UserArr.unshift(two)
        } 
        if(one.companyName){
          UserArr.unshift(one)
        }
        // console.log(UserArr1,UserArr2,UserArr3)
        this.userList = this.userList.concat(UserArr)
        this.total = res.data.total
        // eslint-disable-next-line no-plusplus
        this.page++
        this.loading = false// 加载状态结束
      })
    },
    openUser(id,openType) {
      // console.log()
      this.$router.push({
        path: '/addContact',
        query: {
          id,
          openType
        },
      })
    },
    setTopUser(id) {      
      topUser({ contactsId: id }).then((res) => {
        console.log('res',res)
        this.total=null
        this.page=1
        this.userList = []
        this.loading=false
        this.finished=false
      })
    },
  },
}
</script>
