            </div>

            <span
              v-if="!item.isRead"
              style="position:absolute;right:0;top:1px;color:#ff0021;"
              class="iconfont iconnew"
            />
          </div>
        </div>
      </div>

      <div
        v-if="PermissionObj['createproject_insert'] != undefined"
        class="tab-footer"
      >
        <van-button
          class="gradual-btn"
          style="width:100%;"
          type="primary"
          to="/addApproval"
        >
          立项
        </van-button>
      </div>
    </div>

    <!-- 留言板弹层 -->
    <van-popup v-model="messageBoard">
      <div class="messageBoard">
        <div class="header">
          留言板
        </div>
        <div
          v-for="(item,idx) in messageLists"
          :key="idx"
          class="container"
        >
          <div class="container_header">
            <span style="float:left;">{{ item.author }}</span>
            <span style="float:right;">{{ item.createTime }}</span>
          </div>
          <div class="container_p">
            {{ item.content }}
          </div>
        </div>
      </div>
    </van-popup>

    <!--新增留言弹层 -->
    <van-popup v-model="AddmessageBoard">
      <div class="messageBoard">
        <div class="header">
          留言板
        </div>
        <div class="container">
          <textarea
            v-model="messageStr"
            :class="messageError ? 'textarea_no' :'textarea_yes'"
            placeholder="请输入留言"
          />
        </div>
        <van-row>
          <van-col span="12">
            <van-button
              class="clear-btn"
              style="width:100%;"
              square
              type="primary"
              @click="messageClear()"
            >
              取消
            </van-button>
          </van-col>
          <van-col span="12">
            <van-button
              class="confirm-btn"
              style="width:100%;"
              square
              type="primary"
              @click="messageSave"
            >
              确定
            </van-button>
          </van-col>
        </van-row>
      </div>
    </van-popup>
  </div>
</template>
<script>
// import { api, get } from "@api";
// import { mapState, mapMutations } from 'vuex'
import { projectList,messageList,addMessages } from '@/service/customer'
import { isNull } from '@/utils'

export default {
  name: 'Creation',
  data() {
    return {
       PermissionObj:this.$store.state.powerList.PermissionObj,///权限
      messageBoard: false,
      AddmessageBoard: false,
      messageStr: '',
      messageError: false,
      PJList:[],
      setContent:'',
      messageLists:[],
    }
  },
  computed: {},
  created() {
    this.getProjectList()
  },
  methods: {
    
    getProjectList() {
      const params = {
        menuType: 1,
        size:1000
      }
      projectList(params).then((res) => {
        console.log('projectList', res)
        this.PJList = res.data
      })
    },
    
    //唤醒留言表单
    addMessage(item) {
      console.log('item',item)
      this.setingItem = item
      this.messageBoard = false
      setTimeout(() => {
        // 加载状态结束
        this.AddmessageBoard = true
      }, 300)
    },
    //展示留言
    showMessage(item){
      this.messageBoard = !this.messageBoard
      this.setingMessage = item
      item.isRead = true
      this.getMessageList(item)
    },
    //获取留言列表
    getMessageList(item){
      console.log('获取留言列表',item)
      let params = {
        eventType:5,
        eventId:item.id,
        messageId:item.messageId,
      }
      messageList(params).then(res=>{
        this.messageLists = res.data || []
        console.log('获取留言',res)
      })
    },
    //新增留言
    setMessage(){      
      let params = {
        author:this.$store.getters.userInfo.name,
        authorUserId:this.$store.getters.userInfo.id,
        eventId:this.setingItem.eventId,
        content:this.messageStr,
        eventType:5
      }
      console.log('新增留言',params)
      addMessages(params).then(res=>{
        console.log('获取留言',res)
        this.messageStr = ''
        this.messageError = false     
        this.AddmessageBoard = false
        this.getInit()
        this.getProjectList()
      })
    },
    getInit(){
      this.page=1
      this.pageLog = 1
      this.PJList = []
      this.totalLog = null
      this.total = null
    },
    // 留言板取消事件
    messageClear() {      
      this.AddmessageBoard = false
      this.messageStr = ''
    },
    // 留言保存事件
    messageSave() {
      console.log('留言保存事件',this.messageStr)
      this.setMessage()
      if (isNull(this.messageStr)) {
        this.messageError = true
        return
      }      
    },
    //事件详情
    itemDetails(id){
      this.$router.push({
        path:'/addApproval',
        query:{
          id,
        }
      })
    }
  },
}
</script>
